import React from 'react';
import { Link } from '@reach/router';

import About from './About';
import CalculusI from './Classes/CalcI/CalculusI';
import Home from './Home';
import Review from './Classes/CalcI/Review';

export const PATHS = [
  {
    id: '0',
    path: '',
    name: 'Home',
    component: Home,
  },
  {
    id: '1',
    path: 'About',
    name: 'About',
    component: About,
  },
  {
    id: '2',
    path: 'Classes',
    name: 'Classes',
    children: [
      {
        id: '3',
        path: 'CalcI',
        name: 'Calculus I',
        component: CalculusI,
        children: [
          {
            id: '4',
            path: 'IntroReview',
            name: 'Review',
            component: Review,
          },
        ],
      },
    ],
  },
];

const Navigation = () => (
  <nav>
    <ul>{PATHS.map(element => link(element, ''))}</ul>
  </nav>
);

/* Return component to be rendered */
function link(element, basePath) {
  let fullPath = `${basePath}/${element.path}`;
  if (element.children !== undefined) {
    return (
      <div key={element.id}>
        <li>
          {element.component ? (
            <Link to={fullPath}>{element.name}</Link>
          ) : (
            element.name
          )}
        </li>
        <ul>{element.children.map(child => link(child, fullPath))}</ul>
      </div>
    );
  }
  return (
    <li key={element.id}>
      <Link to={fullPath}>{element.name}</Link>{' '}
    </li>
  );
}

export default Navigation;
